import React from 'react';

// Functional component for displaying information about invoice preferences
const Demo2Preferences = () => {
  return (
    <div>
      <h2>Demo 2: Invoice Preferences</h2>
      <p>
        {/* Explanation of how invoice preferences are managed in the Home.js component */}
        In the `Home.js` component, buttons are provided to allow users to customize their invoice preferences. These buttons enable users to change the font size, text color, and background color of the invoice. The preferences are stored in the component's state and are saved to local storage, ensuring that the user's choices persist across sessions. The `handlePreferenceChange` function updates the state and local storage whenever a button is clicked.
      </p>
    </div>
  );
};

export default Demo2Preferences;