import React, { useEffect, useState } from "react";
import $ from "jquery";
import "jquery-ui/ui/widgets/draggable";
import "jquery-ui/ui/widgets/droppable";

function FormCourtesyPhone({ onBondChange, isBusiness, setCourtesyItems }) {
  // Initial list of courtesy items with their details
  const initialCourtesyList = [
    { id: "1", type: "phone", name: "iPhone 10", bond: 275, image: '/images/iphone10.jfif' },
    { id: "2", type: "phone", name: "iPhone 14", bond: 300, image: '/images/iphone14.jpg' },
    { id: "3", type: "phone", name: "iPhone 16", bond: 500, image: '/images/iphone16.jpg' },
    { id: "4", type: "phone", name: "Samsung galaxy", bond: 200, image: '/images/samsunggalaxy.jfif' },
    { id: "5", type: "phone", name: "Nokia", bond: 150, image: '/images/nokia.jpg' },
    { id: "6", type: "phone", name: "Xiaomi", bond: 100, image: '/images/xiaomi.jpg' },
    { id: "7", type: "charger", name: "iPhone charger", bond: 45 },
    { id: "8", type: "charger", name: "Samsung charger", bond: 30 },
    { id: "9", type: "charger", name: "Nokia charger", bond: 25 },
    { id: "10", type: "charger", name: "Xiaomi charger", bond: 25 },
  ];

  // State to track selected phone and charger
  const [selectedPhone, setSelectedPhone] = useState(null);
  const [selectedCharger, setSelectedCharger] = useState(null);

  useEffect(() => {
    // Initialize draggable functionality for phone items
    $(".draggable").draggable({
      revert: true,
    });

    // Initialize droppable area for dropping phone items
    $(".droppable").droppable({
      accept: ".draggable",
      drop: (event, ui) => {
        const phoneId = ui.draggable.data("id").toString();
        console.log("Dropped phone ID:", phoneId);
        const selectedItem = initialCourtesyList.find((item) => item.id === phoneId);

        if (selectedItem) {
          const bond = isBusiness ? 0 : selectedItem.bond;
          const phoneItem = { name: selectedItem.name, bond };

          console.log("Selected phone item:", phoneItem);
          setSelectedPhone(phoneItem);
          setCourtesyItems((prev) => ({ ...prev, phone: phoneItem }));
          updateBond(phoneItem, selectedCharger);
        } else {
          console.error("Selected item not found");
        }
      },
    });
  }, [isBusiness, selectedCharger, setCourtesyItems]);

  const updateBond = (phone, charger) => {
    const totalBond = isBusiness ? 0 : (phone ? phone.bond : 0) + (charger ? charger.bond : 0);
    onBondChange(totalBond);
  };

  const handleChargerChange = (event) => {
    const chargerId = event.target.value;
    if (chargerId === "none") {
      setSelectedCharger(null);
      setCourtesyItems((prev) => ({ ...prev, charger: null }));
      updateBond(selectedPhone, null);
      return;
    }

    const selectedItem = initialCourtesyList.find((item) => item.id === chargerId);
    const bond = isBusiness ? 0 : selectedItem.bond;
    const chargerItem = { name: selectedItem.name, bond };
    setSelectedCharger(chargerItem);
    setCourtesyItems((prev) => ({ ...prev, charger: chargerItem }));
    updateBond(selectedPhone, chargerItem);
  };

  const removeSelectedPhone = () => {
    setSelectedPhone(null);
    setCourtesyItems((prev) => ({ ...prev, phone: null }));
    updateBond(null, selectedCharger);
  };

  return (
    <>
      <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
        <h2>Courtesy Phone</h2>
        <button onClick={removeSelectedPhone} disabled={!selectedPhone}>
          Remove Phone
        </button>
      </div>
      <div className="row mt-2 ms-3" style={{ display: "grid", gridTemplateColumns: "repeat(3, 1fr)", gap: "10px" }}>
        {initialCourtesyList
          .filter((item) => item.type === "phone")
          .map((phone) => (
            <div key={phone.id} className="draggable" data-id={phone.id}>
              <img
                src={phone.image}
                alt={phone.name}
                style={{
                  width: "100px",
                  cursor: "pointer",
                }}
              />
            </div>
          ))}
      </div>
      <h4 className="ms-4 mt-2">Choose a Charger</h4>
      <div className="row mt-2 ms-3">
        <label className="col-12 col-md-12 col-lg-4">Item Type </label>
        <select
          className="col-12 col-md-12 col-lg-7"
          onChange={handleChargerChange}
          value={selectedCharger ? selectedCharger.id : "none"}
        >
          <option value="none">None</option>
          {initialCourtesyList
            .filter((item) => item.type === "charger")
            .map((charger) => (
              <option key={charger.id} value={charger.id}>
                {charger.name}
              </option>
            ))}
        </select>
      </div>
      <div className="row mt-2 ms-3 me-3 bg-white droppable" style={{ minHeight: "100px", border: "1px dashed #ccc" }}>
        <table className="table table-bordered">
          <thead>
            <tr>
              <th>Item</th>
              <th>Bond</th>
            </tr>
          </thead>
          <tbody>
            {selectedPhone ? (
              <tr>
                <td>{selectedPhone.name}</td>
                <td>{isBusiness ? 0 : selectedPhone.bond}</td>
              </tr>
            ) : (
              <tr>
                <td colSpan="2" className="text-center">
                  Drag a phone here
                </td>
              </tr>
            )}
            {selectedCharger && (
              <tr>
                <td>{selectedCharger.name}</td>
                <td>{isBusiness ? 0 : selectedCharger.bond}</td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </>
  );
}

export default FormCourtesyPhone;
