// Function Component for rendering the footer
function Footer() {
    // Inline styles for the footer
    const footerStyle = {
        minHeight: '10vh', // Minimum height of the footer
        fontSize: '1.2em', // Font size for the text
        color: 'white', // Text color
        backgroundColor: '#2C3E50', // Background color
        display: 'flex', // Use flexbox for layout
        justifyContent: 'center', // Center content horizontally
        alignItems: 'center' // Center content vertically
    };

    return (
        <>
            <footer style={footerStyle}>
                <p className='text-center'>Advanced Web App Development. @2024.</p>
            </footer>
        </>
    );
}

export default Footer;