//Import all dependencies, other Components
import Header from "./my-components/Header";
import Footer from "./my-components/Footer";
import Home from "./my-components/Home";
import AdvancedJS from "./my-components/AdvancedJS";
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import FAQ from "./my-components/FAQ";
import Invoice from "./my-components/Invoice";

//Function Component "App"
function App() {

  //Component UI: HTML Rendering
  return (
    <> {/* React Fragment: serves as a parent component in JSX and doesn't add anything to the DOM */}
      <h1 className='bg-warning p-3 text-center'>ITWD6.408: PROJECT 2</h1>
      <Router> {/* Sets up routing for the application */}
        <Header /> {/* Call Header component */}
        <Routes> {/* Define routes for different components */}
          <Route path="/" element={<Home />}/> {/* Home page route */}
          <Route path="/advancedjs" element={<AdvancedJS />}/> {/* AdvancedJS page route */}
          <Route path="/faq" element={<FAQ />}/> {/* FAQ page route */}
          <Route path="/invoice" element={<Invoice />}/> {/* Invoice page route */}
        </Routes>
        <Footer /> {/* Call Footer component */}
      </Router>
    </>
  );
}

//Export this component to the entire app, can be re-used or hooked into other Components
export default App;