import React, { useState } from "react";

// Function Component
function FormRepairDetail({ onWarrantyChange, setRepairDetails }) {
  // State to manage form data
  const [purchaseDate, setPurchaseDate] = useState("");
  const [repairDate, setRepairDate] = useState("");
  const [warrantyDisabled, setWarrantyDisabled] = useState(false);
  const [imei, setImei] = useState("");
  const [faultCategory, setFaultCategory] = useState("Battery");
  const [underWarranty, setUnderWarranty] = useState(false);
  const [make, setMake] = useState("Apple");
  const [image, setImage] = useState(null);

  // Initialize default values in repair details directly
  const handleInputChange = (e) => {
    const { id, value } = e.target;
    setRepairDetails((prev) => ({...prev, [id]: value }));
    if (id === "make") setMake(value);
    if (id === "faultCategory") setFaultCategory(value);
  }

  // Helper function to check if the purchase date is older than 24 months
  const isWarrantyExpired = (purchaseDate) => {
    const purchase = new Date(purchaseDate);
    const today = new Date();
    const monthsDiff =
      (today.getFullYear() - purchase.getFullYear()) * 12 +
      (today.getMonth() - purchase.getMonth());
    return monthsDiff > 24;
  };

  // Handler for Purchase Date input change
  const handlePurchaseDateChange = (e) => {
    const newPurchaseDate = e.target.value;
    setPurchaseDate(newPurchaseDate);
    handleInputChange(e);

    // Disable the warranty checkbox if purchase date is greater than 24 months
    if (newPurchaseDate && isWarrantyExpired(newPurchaseDate)) {
      setWarrantyDisabled(true);
      setUnderWarranty(false);
    } else {
      setWarrantyDisabled(false);
    }

    onWarrantyChange(underWarranty);
  };

  // Handler for Repair Date input change
  const handleRepairDateChange = (e) => {
    const newRepairDate = e.target.value;
    setRepairDate(newRepairDate);
    handleInputChange(e);
  };

  // Handler for IMEI input change
  const handleImeiChange = (e) => {
    const value = e.target.value;
    if (value.length <= 15) {
      setImei(value);
      handleInputChange(e);
    }
  };

  const handleMakeChange = (e) => {
    handleInputChange(e);
  };

  const handleFaultCategoryChange = (e) => {
    handleInputChange(e);
  };

  const handleWarrantyChange = () => {
    const newWarrantyState = !underWarranty;
    setUnderWarranty(newWarrantyState);
    onWarrantyChange(newWarrantyState);
    handleInputChange({ target: { id: "warranty", value: newWarrantyState } });
  };

  // Helper function to get today's date in YYYY-MM-DD format for date validation
  const getTodayDate = () => {
    const today = new Date();
    return today.toISOString().split("T")[0];
  };

  const getTomorrowDate = () => {
    const tomorrow = new Date();
    tomorrow.setDate(tomorrow.getDate() + 1); // Adds 1 day to todays date
    return tomorrow.toISOString().slice(0, 16); // Slices to get "YYYY-MM-DDTHH:MM"
  };

  const handleImageUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setImage(reader.result);
        localStorage.setItem("repairImage", reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  return (
    <>
      <h2>Repair Details</h2>
      {/* Purchase Date */}
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Purchase Date: *</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="date"
          id="purchaseDate"
          value={purchaseDate}
          onChange={handlePurchaseDateChange}
          max={getTodayDate()}
          required
        />
      </div>
      {/* Repair Date */}
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Repair Date: *</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="datetime-local"
          id="repairDate"
          value={repairDate}
          onChange={handleRepairDateChange}
          min={getTomorrowDate()}
          required
        />
      </div>
      {/* Under Warranty */}
      <div className="row">
        <fieldset className="border border-primary col-12 col-lg-11 ms-1 me-4 mb-3">
          <legend className="col-11 float-none w-auto">Under Warranty</legend>
          <div>
            <label className="col-12 col=md-12 col-lg-4">Warranty</label>
            <input
              type="checkbox"
              id="warranty"
              checked={underWarranty}
              disabled={warrantyDisabled}
              onChange={handleWarrantyChange}
            />
          </div>
        </fieldset>
      </div>
      {/* Other details */}
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">IMEI: *</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="number"
          id="imei"
          value={imei}
          onChange={handleImeiChange}
          pattern="^\d{15}$"
          title="IMEI must be exactly 15 digits."
          required
        />
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Make: *</label>
        <select
          className="col-12 col-md-12 col-lg-7"
          id="make"
          value={make}
          onChange={handleMakeChange}
          required
        >
          <option value="Apple">Apple</option>
          <option value="LG">LG</option>
          <option value="Motorola">Motorola</option>
          <option value="Nokia">Nokia</option>
          <option value="Samsung">Samsung</option>
          <option value="Sony">Sony</option>
          <option value="Other">Other</option>
        </select>
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Model Number:</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="text"
          id="modelNumber"
          onChange={(e) => setRepairDetails((prev) => ({ ...prev, modelNumber: e.target.value }))}
        />
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Fault Category: *</label>
        <select
          className="col-12 col-md-12 col-lg-7"
          id="faultCategory"
          value= {faultCategory}
          onChange={handleFaultCategoryChange}
          required
        >
          <option value="Battery">Battery</option>
          <option value="Charging">Charging</option>
          <option value="Screen">Screen</option>
          <option value="SD-Storage">SD-Storage</option>
          <option value="Software">Software</option>
          <option value="Other">Other</option>
        </select>
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Description: *</label>
        <textarea
          className="col-12 col-md-12 col-lg-7"
          rows="8"
          type="text"
          id="description"
          onChange={(e) => setRepairDetails((prev) => ({...prev, description: e.target.value}))}
          required
        />
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Upload Image:</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="file"
          accept="image/*"
          onChange={handleImageUpload}
        />
      </div>
    </>
  );
}

export default FormRepairDetail;
