import React from 'react';
import Slideshow from './Slideshow';
import ManualSlideshow from './ManualSlideshow';
import Questions from './faqs.json';
import { useState } from 'react';

// Function Component for displaying FAQs
function FAQ() {
    // State to store the entered keywords for filtering questions
    const [enteredKeywords, setEnteredKeywords] = useState('');
    let id = 0; // Unique key for each question

    // Component UI: HTML Rendering
    return (
        <>
            <div style={{ minHeight: '60vh' }}>
                <h1>Frequently Asked Questions</h1>

                {/* Input field for entering keywords to filter questions */}
                <div className="row">
                    <input
                        className='col-11 m-3'
                        type='text'
                        name='search'
                        onChange={(e) => { setEnteredKeywords(e.target.value) }}
                        placeholder='Keywords'
                    />
                </div>

                {/* Slideshow components */}
                <div>
                    <Slideshow />
                    <ManualSlideshow />
                </div>

                {/* Loop through all questions and display them */}
                <div>
                    {Questions.filter(
                        question => question.question.toLowerCase().includes(enteredKeywords.toLowerCase()) || question.answer.toLowerCase().includes(enteredKeywords.toLowerCase())
                    ).map(
                        question => {
                            return (
                                <div className='bg-warning p-3 m-2' key={id++}>
                                    <h4>{question.question}</h4>
                                    <p>{question.answer}</p>
                                </div>
                            )
                        }
                    )}
                </div>
            </div>
        </>
    );
}

export default FAQ;