import React from 'react';

// Functional component for demonstrating the autocomplete feature
const Demo1Autocomplete = () => {
  return (
    <div>
      <h2>Demo 1: Autocomplete Address</h2>
      <p>
        {/* Description of how the autocomplete feature is implemented in the FormCustomerDetail.js component */}
        In the `FormCustomerDetail.js` component, the Google Maps Places API is used to implement an autocomplete feature for the address input field. This is achieved by initializing the `Autocomplete` object from the Google Maps library, which listens for changes in the input field. When a place is selected, the address components are extracted and populated into the respective fields (street, suburb, city, and postcode) using the `setCustomerDetails` function to update the state.
      </p>
    </div>
  );
};

export default Demo1Autocomplete;