import { Link } from "react-router-dom";

// Function Component for rendering form buttons
function FormButtons() {
  // Component UI: HTML Rendering
  return (
    <>
      <div className="row justify-content-center" style={{ margin: "0 15px" }}>
        {/* Submit button */}
        <input
          type="submit"
          className="col-12 col-md-12 col-lg-2 btn me-3 mb-2 text-dark bg-white"
          style={{
            border: "1px solid #000",
            padding: "10px 0",
            borderRadius: "0",
          }}
          value="SUBMIT"
        />
        {/* Reset button */}
        <input
          type="reset"
          className="col-12 col-md-12 col-lg-2 btn me-3 mb-2 text-dark bg-white"
          style={{
            border: "1px solid #000",
            padding: "10px 0",
            borderRadius: "0",
          }}
          value="RESET"
        />
        {/* Link to FAQ page */}
        <Link className="col-12 col-md-12 col-lg-2 p-0 ms-0" to="/faq">
          <input
            type="button"
            className="btn text-dark bg-white"
            style={{
              border: "1px solid #000",
              padding: "10px 0",
              borderRadius: "0",
              width: "100%",
            }}
            value="FAQ"
          />
        </Link>
      </div>
    </>
  );
}

export default FormButtons;
