import React from 'react';

// Functional component for displaying information about the slideshow demos
const Demo3Slideshows = () => {
  return (
    <div>
      <h2>Demo 3: Slideshows</h2>
      <p>
        {/* Explanation of how the slideshow components are implemented */}
        The slideshows are implemented using two components: `Slideshow` and `ManualSlideshow`. The `Slideshow` component utilizes the `react-slick` library to create an automatic slideshow that cycles through images with a specified speed and autoplay settings. The `ManualSlideshow` component, on the other hand, allows users to manually navigate through images using "Previous" and "Next" buttons. It displays a set of images at a time and updates the current index based on user interaction.
      </p>
    </div>
  );
};

export default Demo3Slideshows;