import { Link } from 'react-router-dom';

// Function Component
function Header() {

    // Styles for the header and tagline
    const headerStyle = {
        minHeight: '15vh',
        backgroundColor: '#2C3E50'
    };
    const taglineStyle = {
        minHeight: '15vh',
        backgroundColor: '#2C3E50'
    }

    return (
        <>
            <header className="row" style={headerStyle}>
                {/* Tagline for the booking system */}
                <div className="col-12 col-md-12 col-lg-8 text-center text-white display-5" style={taglineStyle}>
                    Phone Fix Booking System
                </div>

                {/* Navigation links */}
                <div className="col-12 col-md-12 col-lg-4">
                    <div className="row">
                        <Link to="/" className="col-12 col-md-6 col-lg-6 bg-info p-0 m-0 border border-dark text-center text-white" style={{ textDecoration: 'none' }}>
                            HOME
                        </Link>
                        <Link to="/advancedJS" className="col-12 col-md-6 col-lg-6 bg-info p-0 m-0 border border-dark text-center text-white" style={{ textDecoration: 'none' }}>
                            EXTENSTION
                        </Link>
                    </div>
                </div>
            </header>
        </>
    );
}

export default Header;