import React, { useState } from 'react';
import Demo1Autocomplete from './Demo1Autocomplete';
import Demo2Preferences from './Demo2Preferences';
import Demo3Slideshows from './Demo3Slideshows';
import Demo4Map from './Demo4Map';
import Demo5DragDrop from './Demo5DragDrop';

// Main component for rendering advanced JavaScript demos
const AdvancedJS = () => {
  // State to track which demo is currently active
  const [activeDemo, setActiveDemo] = useState(1);

  // Function to render the content of the active demo
  const renderDemoContent = () => {
    switch (activeDemo) {
      case 1:
        return <Demo1Autocomplete />;
      case 2:
        return <Demo2Preferences />;
      case 3:
        return <Demo3Slideshows />;
      case 4:
        return <Demo4Map />;
      case 5:
        return <Demo5DragDrop />;
      default:
        return <div>Select a demo</div>;
    }
  };

  return (
    <div className="advanced-js-page" style={{ display: 'flex', flexDirection: 'column', height: '100vh' }}>
      {/* Statement of authenticity section */}
      <div style={{ backgroundColor: '#e8f5e9', padding: '10px', marginBottom: '10px' }}>
        <h4>Statement of authenticity</h4>
        <p>
          I confirm that:
          <ul>
            <li>This is an original assessment and is entirely my own work.</li>
            <li>It contains no material previously published or written by another person or myself except where due acknowledgement is made in the text.</li>
            <li>No material which to a substantial extent, has been submitted for any other academic course, is included without acknowledgement.</li>
          </ul>
        </p>
      </div>
      {/* Main content area with sidebar and demo content */}
      <div style={{ display: 'flex', flex: 1 }}>
        {/* Sidebar with buttons to select different demos */}
        <div className="sidebar" style={{ width: '20%', backgroundColor: '#f0ad4e', padding: '10px' }}>
          {[1, 2, 3, 4, 5].map((demo) => (
            <button
              key={demo}
              onClick={() => setActiveDemo(demo)}
              style={{
                display: 'block',
                width: '100%',
                padding: '10px',
                margin: '5px 0',
                backgroundColor: activeDemo === demo ? '#337ab7' : '#5bc0de',
                color: 'white',
                border: 'none',
                cursor: 'pointer',
              }}
            >
              Demo {demo}
            </button>
          ))}
        </div>
        {/* Content area where the selected demo is displayed */}
        <div className="content" style={{ width: '80%', padding: '20px', backgroundColor: '#e6e6e6' }}>
          <div style={{ border: '1px solid #ccc', padding: '20px', backgroundColor: '#f7f7f7' }}>
            {renderDemoContent()}
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdvancedJS;