import React, { useEffect, useRef, useState } from 'react';
import { LoadScript } from '@react-google-maps/api';

// Function Component
function FormCustomerDetail({ onCustomerTypeChange, setCustomerDetails }) {
  // Regular expression patterns for validation
  const namePattern = "^[A-Za-z\\s\\-]+$"; // Pattern for validating names
  const phonePattern = "^[0-9\\s\\(\\)\\-\\+]+$"; // Pattern for validating phone numbers
  const postcodePattern = "^[0-9]{4}$"; // Pattern for validating postcodes

  const [title, setTitle] = useState("Mr"); // State for storing the selected title
  const streetInputRef = useRef(null); // Ref for street input field
  const suburbInputRef = useRef(null); // Ref for suburb input field
  const cityInputRef = useRef(null); // Ref for city input field
  const postCodeInputRef = useRef(null); // Ref for postcode input field

  useEffect(() => {
    const initializeAutocomplete = () => {
      if (window.google && window.google.maps && window.google.maps.places) {
        // Initialize Google Places Autocomplete
        const autocomplete = new window.google.maps.places.Autocomplete(streetInputRef.current, {
          types: ["geocode"],
        });

        // Add listener for place changes
        autocomplete.addListener("place_changed", () => {
          const place = autocomplete.getPlace();
          if (place.address_components) {
            const address = {
              street: "",
              suburb: "",
              city: "",
              postCode: "",
            };

            // Map address components to address fields
            place.address_components.forEach(component => {
              const componentType = component.types[0];
              switch (componentType) {
                case "street_number":
                  address.street = component.long_name + " " + address.street;
                  break;
                case "route":
                  address.street += component.long_name;
                  break;
                case "sublocality_level_1":
                case "sublocality":
                  address.suburb = component.long_name;
                  break;
                case "locality":
                  address.city = component.long_name;
                  break;
                case "postal_code":
                  address.postCode = component.long_name;
                  break;
                default:
                  break;
              }
            });

            console.log("Address:", address);

            // Update input fields with address details
            if (streetInputRef.current) streetInputRef.current.value = address.street;
            if (suburbInputRef.current) suburbInputRef.current.value = address.suburb;
            if (cityInputRef.current) cityInputRef.current.value = address.city;
            if (postCodeInputRef.current) postCodeInputRef.current.value = address.postCode;

            // Update customer details state
            setCustomerDetails((prev) => ({ ...prev, ...address }));
          }
        });
      }
    };

    initializeAutocomplete();
  }, [setCustomerDetails]);

  const handleCustomerTypeChange = (e) => {
    onCustomerTypeChange(e.target.value);
    handleInputChange(e);
  };

  const handleInputChange = (e) => {
    const { id, value } = e.target;
    setCustomerDetails((prev) => ({ ...prev, [id]: value }));
    if (id === "title") setTitle(value);
  };

  // Component UI: HTML Rendering
  return (
    <LoadScript googleMapsApiKey="AIzaSyA3ttrq9h77BPEs2UckoOg814u2_yxbHgk" libraries={['places']}>
      <h2>Customer Details</h2>
      {/* Customer type */}
      <div className="row">
        <fieldset className="border border-primary col-12 col-lg-11 ms-2 me-4">
          <legend className="col-11 float-none w-auto">Customer type *</legend>
          <div>
            <label className="col-12 col-md-12 col-lg-4">Customer</label>
            <input
              type="radio"
              id="customerType"
              name="customer-type"
              value="customer"
              onChange={handleCustomerTypeChange}
              defaultChecked
              required
            />
          </div>
          <div>
            <label className="col-12 col-md-12 col-lg-4">Business</label>
            <input
              type="radio"
              id="businessType"
              name="customer-type"
              value="business"
              onChange={handleCustomerTypeChange}
              required
            />
          </div>
        </fieldset>
      </div>

      {/* Details */}
      <div className="row mt-2">
        <label className="col-12 col-md-12 col-lg-4">Title: *</label>
        <select className="col-12 col-md-12 col-lg-7" required id="title" value={title} onChange={handleInputChange}>
          <option value="Mr">Mr</option>
          <option value="Mrs">Mrs</option>
          <option value="Ms">Ms</option>
          <option value="Miss">Miss</option>
          <option value="Dr">Dr</option>
        </select>
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">First name: *</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="text"
          id="fname"
          pattern={namePattern}
          onChange={handleCustomerTypeChange}
          title="First name should only contain alphabetical characters, spaces, and hyphen (-)."
          required
        />
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Last name: *</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="text"
          id="lname"
          pattern={namePattern}
          onChange={handleCustomerTypeChange}
          title="Last name should only contain alphabetical characters, spaces, and hyphen (-)."
          required
        />
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Street: *</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="text"
          onChange={handleCustomerTypeChange}
          id="street"
          ref={streetInputRef}
          required
        />
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Suburb:</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="text"
          id="suburb"
          ref={suburbInputRef}
          onChange={handleCustomerTypeChange}
        />
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">City: *</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="text"
          id="city"
          ref={cityInputRef}
          onChange={handleCustomerTypeChange}
          required
        />
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Post Code:</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="text"
          id="postCode"
          ref={postCodeInputRef}
          pattern={postcodePattern}
          title="Post code must be exactly 4 digits."
          onChange={handleCustomerTypeChange}
          maxLength="4"
        />
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Phone Number: *</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="text"
          id="phoneNumber"
          pattern={phonePattern}
          onChange={handleCustomerTypeChange}
          title="Phone number should only contain numbers, spaces, ( ), -, and + symbols."
          required
        />
      </div>
      <div className="row mt-1">
        <label className="col-12 col-md-12 col-lg-4">Email: *</label>
        <input
          className="col-12 col-md-12 col-lg-7"
          type="text"
          id="email"
          required
          onChange={handleCustomerTypeChange}
        />
      </div>
    </LoadScript>
  );
};

export default FormCustomerDetail;
