import React from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

const Slideshow = () => {
  // Array of image paths for the slideshow
  const images = [
    '/images/iphone10.jfif',
    '/images/iphone14.jpg',
    '/images/iphone16.jpg',
    '/images/samsunggalaxy.jfif',
    '/images/nokia.jpg',
    '/images/xiaomi.jpg'
  ];

  // Settings for the react-slick slider
  const settings = {
    infinite: true, // Enables infinite looping of slides
    slidesToShow: 3, // Number of slides to show at once
    slidesToScroll: 1, // Number of slides to scroll at a time
    autoplay: true, // Enables automatic sliding
    speed: 2000, // Transition speed in milliseconds
    autoplaySpeed: 2000, // Delay between each auto slide transition
  };

  return (
    <div className="slideshow">
      <Slider {...settings}>
        {images.map((image, index) => (
          <div key={index}>
            <img src={image} alt={`Slide ${index}`} style={{ maxWidth: '300px', maxHeight: '200px', margin: '0 auto' }} />
          </div>
        ))}
      </Slider>
    </div>
  );
};

export default Slideshow;