import React from 'react';

// Functional component for displaying information about the drag-and-drop demo
const Demo5DragDrop = () => {
  return (
    <div>
      <h2>Demo 5: Drag and Drop</h2>
      <p>
        {/* Explanation of how drag-and-drop functionality is implemented */}
        In the `FormCourtesyPhone.js` component, drag-and-drop functionality is implemented using jQuery UI. The phones are made draggable by applying the `.draggable()` method, allowing them to be moved around the interface. The table is set as a droppable area using the `.droppable()` method, which accepts the draggable items. When a phone is dropped onto the table, the `drop` event handler updates the state with the selected phone's details, including its name and bond, and displays it in the table.
      </p>
    </div>
  );
};

export default Demo5DragDrop;