import { useLocation } from "react-router-dom";
import { useState, useEffect } from "react";

function Invoice() {
  const { state } = useLocation();
  const { customerDetails, repairDetails, bond, underWarranty, courtesyItems, costDetails, isBusiness } = state || {};
  const { phone, charger } = courtesyItems || {};
  const { serviceFee, total, GST, totalGST } = costDetails;
  console.log(repairDetails);
  
  // State to hold the unique job number
  const [jobNumber, setJobNumber] = useState(null);

  // Generate and set a unique job number on component load
  useEffect(() => {
    const currentJobNumber = parseInt(localStorage.getItem('jobNumber') || "3000", 10);
    setJobNumber(currentJobNumber);
    localStorage.setItem('jobNumber', currentJobNumber + 1);
  }, []);

  // Updated formatDate function with a format type parameter
  function formatDate(date, formatType = 'standard', withTime = false) {
    const day = date.getDate().toString().padStart(2, '0');
    const month = (date.getMonth() + 1).toString().padStart(2, '0');
    const year = date.getFullYear();

    if (formatType === 'ddmmyyyy') {
      // Format as "DD/MM/YYYY" or "DD/MM/YYYY - HH:MMam/pm"
      let formattedDate = `${day}/${month}/${year}`;
      if (withTime) {
        let hours = date.getHours();
        const minutes = date.getMinutes().toString().padStart(2, '0');
        const ampm = hours >= 12 ? 'pm' : 'am';
        hours = hours % 12 || 12; // Convert 0 hour to 12 for 12-hour format
        formattedDate = `${formattedDate} - ${hours}:${minutes}${ampm}`;
      }
      return formattedDate;
    } else {
      // Default to "Month DD, YYYY - HH:MM" for invoice and due dates
      const months = [
        "January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"
      ];
      let formattedDate = `${months[date.getMonth()]} ${day}, ${year}`;
      if (withTime) {
        const hours = date.getHours().toString().padStart(2, '0');
        const minutes = date.getMinutes().toString().padStart(2, '0');
        formattedDate = `${formattedDate} - ${hours}:${minutes}`;
      }
      return formattedDate;
    }
  }
  
  // Calculate Invoice Date and Payment Due Date
  const invoiceDate = new Date();
  const paymentDueDate = new Date(invoiceDate);
  paymentDueDate.setDate(invoiceDate.getDate() + 5);

  const formattedInvoiceDate = formatDate(invoiceDate, 'standard', true);
  const formattedPaymentDueDate = formatDate(paymentDueDate, 'standard');

  // Load preferences from local storage
  const preferences = JSON.parse(localStorage.getItem("preferences")) || {
    fontSize: "16px",
    textColor: "#000000",
    backgroundColor: "#ffffff",
  };

  const storedImage = localStorage.getItem("repairImage");

  return (
    <div
      className="container border"
      style={{
        maxWidth: "80%",
        fontFamily: "Arial, sans-serif",
        fontSize: preferences.fontSize,
        color: preferences.textColor,
        backgroundColor: preferences.backgroundColor,
      }}
    >
      <div className="py-2 bg-secondary row">
        <h2 className="text-start p-4 col-6 fw-bold fs-1">Repair Booking</h2>
        <div className="col-6 row text-end fs-2">
            <p className="col-12 p-0 m-0 me-1 align-text-bottom">Amount Due</p>
            <p className="col-12 fw-bold me-1">${totalGST.toFixed(2)}</p>
        </div>
      </div>
      
      {/* Customer and Job Information */}
      <div className="row py-3">
        <div className="col-md-6 border-bottom border-dark ms-4">
          <h5 className="fw-bold">Customer</h5>
          <p>
            {customerDetails?.title} {customerDetails?.fname} {customerDetails?.lname}<br />
            {customerDetails?.street}<br />
            {customerDetails?.suburb}, {customerDetails?.city} {customerDetails?.postCode}<br />
            {customerDetails?.phoneNumber}<br />
            {customerDetails?.email}
          </p>
        </div>
        <div className="col-md-6 row">
          <h5 className="col-10 fw-bold text-center">Repair Job</h5>
          <div className="col-2"></div>
          <div className="col-12 row border-bottom border-dark">
            <div className="col-5"></div>
            <div className="col-7">
                <div>
                    <p className="mb-0"><span className="fw-bold">Job Number:</span>&emsp;{jobNumber}</p>
                    <p className="mb-0"><span className="fw-bold">Invoice Date:</span>&emsp;{formattedInvoiceDate}</p>
                    <p className="mb-0"><span className="fw-bold">Payment Due:</span>&emsp;{formattedPaymentDueDate}</p>
                </div>
            </div>
          </div>
        </div>
      </div>

      {/* Repair Details */}
      <div className="row py-3">
        <h5 className="col-12 fw-bold ms-4">Repair Details</h5>
        <div className="col-12 row">
          <div className="col-1"></div>
          <div className="col-11">
            <p className="mb-0"><span className="fw-bold">Purchase Date:</span>&emsp;&emsp; {repairDetails?.purchaseDate ? formatDate(new Date(repairDetails.purchaseDate), 'ddmmyyyy') : ''}</p>
            <p className="mb-0"><span className="fw-bold">Repair Date/Time:</span>&emsp;{repairDetails?.repairDate ? formatDate(new Date(repairDetails.repairDate), 'ddmmyyyy', true) : ''}</p>
            <p className="mb-0"><span className="fw-bold">Under Warranty:</span>&emsp;&nbsp;&ensp;{underWarranty ? "Yes " : "No "}</p>
            <p className="mb-0"><span className="fw-bold">IMEI Number:</span>&emsp;&emsp;&emsp;{repairDetails?.imei}</p>
            <p className="mb-0"><span className="fw-bold">Device Make:</span>&emsp;&emsp;&emsp;{repairDetails?.make}</p>
            <p className="mb-0"><span className="fw-bold">Model Number:</span>&emsp;&emsp;{repairDetails?.modelNumber}</p>
            <p className="mb-0"><span className="fw-bold">Fault Category:</span>&emsp;&emsp;{repairDetails?.faultCategory}</p>
            <p className="mb-0"><span className="fw-bold">Description:</span>&emsp;&emsp;&emsp;&nbsp;{repairDetails?.description}</p>
          </div>  
        </div>
      </div>

      {/* Courtesy Loan Device Details */}
      <div className="row py-3">
        <h5 className="col-12 fw-bold ms-4">Courtesy Loan Device Details</h5>
        <div className="col-12 row">
          <div className="col-1"></div>
          <div className="col-3">
            <table className="table table-bordered border-dark">
              <thead>
                <tr>
                  <th className="text-center">Item</th>
                  <th className="text-center">Cost</th>
                </tr>
              </thead>
              <tbody>
                {phone && (
                  <tr>
                    <td>{phone.name}</td>
                    <td>${phone.bond}</td>
                  </tr>
                )}
                {charger && (
                  <tr>
                    <td>{charger.name}</td>
                    <td>${charger.bond}</td>
                  </tr>
                )}
                {!phone && !charger && (
                  <tr>
                    <td colSpan="2" className="text-center">No items borrowed</td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
          <div className="col-8"></div>
        </div>
      </div>

      {/* Repair Image */}
      {storedImage && (
        <div className="row py-3">
          <h5 className="col-12 fw-bold ms-4">Uploaded Image</h5>
          <div className="col-12 row">
            <div className="col-1"></div>
            <div className="col-11">
              <img src={storedImage} alt="Repair" style={{ maxWidth: "100%" }} />
            </div>
          </div>
        </div>
      )}

      {/* Totals Section */}
      <div className="row py-3">
        <h5 className="col-9 text-end fw-bold">Totals</h5>
        <div className="col-3"></div>
        <div className="col-9"></div>
        <div className="col-3">
          <p className="mb-0"><span className="fw-bold">Bond:</span>&emsp;&emsp;&emsp;&emsp;&emsp;${isBusiness ? 0 : bond.toFixed(2)}</p>
          <p className="mb-0"><span className="fw-bold">Service Fee:</span>&emsp;&emsp;${serviceFee.toFixed(2)}</p>
          <p className="mb-0"><span className="fw-bold">Total:</span>&emsp;&emsp;&emsp;&emsp;&emsp;${total.toFixed(2)}</p>
          <p className="mb-0"><span className="fw-bold">GST:</span>&emsp;&emsp;&emsp;&emsp;&emsp;&nbsp;${GST.toFixed(2)}</p>
          <p className="mb-0"><span className="fw-bold">Total(+GST):</span>&emsp;&emsp;${totalGST.toFixed(2)}</p>
        </div>
      </div>

      {/* Footer */}
      <div className="row py-3 border-top">
        <div className="col-6 mt-4">
            <h5 className="fw-bold">Phone Fix Services</h5>
            <p>
              42 Fixed It Drive<br />
              Alexander
            </p>
        </div>
        <div className="col-6 text-end pt-3">
          <h5 className="fw-bold">Contact Us</h5>
          <p><br /><span className="fw-bold">Phone:</span>05876543</p>
        </div>
      </div>
    </div>
  );
}

export default Invoice;
