import React, { useState, useEffect, useRef } from 'react';
import { GoogleMap, LoadScript } from '@react-google-maps/api';

// Styles for the map container
const containerStyle = {
  width: '100%',
  height: '400px'
};

// Default center of the map
const center = {
  lat: -40.9006,
  lng: 174.886
};

// Libraries to load for Google Maps
const libraries = ['marker'];

// List of shop locations
const shops = [
  { id: 1, name: 'Auckland Shop', location: { lat: -36.8509, lng: 174.7645 } },
  { id: 2, name: 'Hamilton Shop', location: { lat: -37.7826, lng: 175.2528 } },
  { id: 3, name: 'Wellington Shop', location: { lat: -41.2865, lng: 174.7762 } },
  { id: 4, name: 'Christchurch Shop', location: { lat: -43.5321, lng: 172.6362 } },
  { id: 5, name: 'Dunedin Shop', location: { lat: -45.8788, lng: 170.5028 } }
];

const Demo4Map = () => {
  const [userLocation, setUserLocation] = useState(null);
  const [nearestShop, setNearestShop] = useState(null);
  const mapRef = useRef(null);

  // Get user's current location on component mount
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const location = {
            lat: position.coords.latitude,
            lng: position.coords.longitude
          };
          setUserLocation(location);
          findNearestShop(location);
        },
        () => {
          console.error('Error fetching location');
        }
      );
    }
  }, []);

  // Calculate distance between two coordinates using the Haversine formula
  const calculateDistance = (lat1, lng1, lat2, lng2) => {
    const toRad = (value) => (value * Math.PI) / 180;
    const R = 6371; // Radius of the Earth in km
    const dLat = toRad(lat2 - lat1);
    const dLng = toRad(lng2 - lng1);
    const a =
      Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(toRad(lat1)) * Math.cos(toRad(lat2)) *
      Math.sin(dLng / 2) * Math.sin(dLng / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    return R * c; // Distance in km
  };

  // Find the nearest shop to the user's location
  const findNearestShop = (location) => {
    const nearest = shops.reduce((prev, curr) => {
      const prevDistance = calculateDistance(location.lat, location.lng, prev.location.lat, prev.location.lng);
      const currDistance = calculateDistance(location.lat, location.lng, curr.location.lat, curr.location.lng);
      return prevDistance < currDistance ? prev : curr;
    });

    setNearestShop(nearest);
  };

  useEffect(() => {
    const loadMarkers = async () => {
      if (mapRef.current && window.google) {
        const { Map } = await window.google.maps.importLibrary('maps');
        const { AdvancedMarkerElement } = await window.google.maps.importLibrary('marker');

        const map = new Map(mapRef.current, {
          center,
          zoom: 5,
          mapId: 'b67ed9f3da2b9ca6'
        });

        shops.forEach((shop) => {
          new AdvancedMarkerElement({
            map,
            position: shop.location,
          });
        });

        if (userLocation) {
          const userContent = document.createElement('div');
          userContent.className = 'marker-label';
          userContent.innerHTML = `<div>You</div>`;

          new AdvancedMarkerElement({
            map,
            position: userLocation,
            content: userContent
          });
        }
      }
    };

    loadMarkers();
  }, [userLocation]);

  return (
    <LoadScript googleMapsApiKey="AIzaSyA3ttrq9h77BPEs2UckoOg814u2_yxbHgk" libraries={libraries}>
      <div>
        <h4>Demo 4: Closest shop map</h4>
        <p>This page demonstrates a map that uses your current location to find the nearest shop from a predefined list. It calculates the distance to each shop and highlights the closest one on the map.</p>
      </div>
      <div ref={mapRef} style={containerStyle} />
      {nearestShop && (
        <div>
          Nearest Shop: {nearestShop.name} - Distance: {calculateDistance(userLocation.lat, userLocation.lng, nearestShop.location.lat, nearestShop.location.lng).toFixed(2)} km
        </div>
      )}
    </LoadScript>
  );
};

export default Demo4Map;