import React, { useState } from 'react';

const ManualSlideshow = () => {
  // Array of image paths for the slideshow
  const images = [
    '/images/iphone10.jfif',
    '/images/iphone14.jpg',
    '/images/iphone16.jpg',
    '/images/samsunggalaxy.jfif',
    '/images/nokia.jpg',
    '/images/xiaomi.jpg'
  ];

  // State to track the current index of the slideshow
  const [currentIndex, setCurrentIndex] = useState(0);

  // Function to move to the next set of slides
  const nextSlide = () => {
    setCurrentIndex((prevIndex) => (prevIndex + 3) % images.length);
  };

  // Function to move to the previous set of slides
  const prevSlide = () => {
    setCurrentIndex((prevIndex) => (prevIndex - 3 + images.length) % images.length);
  };

  return (
    <div className="manual-slideshow" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%' }}>
      <button onClick={prevSlide}>Previous</button>
      <div style={{ display: 'flex', overflow: 'hidden', width: '80%', justifyContent: 'space-between' }}>
        {/* Display a set of 3 images based on the current index */}
        {images.slice(currentIndex, currentIndex + 3).map((image, index) => (
          <div key={index} style={{ flex: '1', display: 'flex', justifyContent: 'center' }}>
            <img src={image} alt={`Slide ${index}`} style={{ maxHeight: '200px', objectFit: 'contain' }} />
          </div>
        ))}
      </div>
      <button onClick={nextSlide}>Next</button>
    </div>
  );
};

export default ManualSlideshow;