//Import all dependencies, other Components
import FormCustomerDetail from "./FormCustomerDetail";
import FormRepairDetail from "./FormRepairDetail";
import FormCourtesyPhone from "./FormCourtesyPhone";
import FormCost from "./FormCost";
import FormButtons from "./FormButtons";
import { useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";

//Function Component
function Home() {
  // State to manage customer details
  const [customerDetails, setCustomerDetails] = useState({});
  // State to manage repair details with default values
  const [repairDetails, setRepairDetails] = useState({
    make: "Apple",            //Default value
    faultCategory: "Battery"  //Default value
  });
  // State to manage bond amount
  const [bond, setBond] = useState(0);
  // State to determine if the customer is a business
  const [isBusiness, setIsBusiness] = useState(false);
  // State to track warranty status
  const [underWarranty, setUnderWarranty] = useState(false);
  // State to manage courtesy items
  const [courtesyItems, setCourtesyItems] = useState({ phone: null, charger: null });
  // State to manage cost details
  const [costDetails, setCostDetails] = useState({});
  // State to manage user preferences
  const [preferences, setPreferences] = useState({
    fontSize: "16px",
    textColor: "#000000",
    backgroundColor: "#ffffff",
  });

  useEffect(() => {
    // Load preferences from local storage on component mount
    const savedPreferences = JSON.parse(localStorage.getItem("preferences"));
    if (savedPreferences) {
      setPreferences(savedPreferences);
    }
  }, []);

  // Function to handle changes in user preferences
  const handlePreferenceChange = (key, value) => {
    const newPreferences = { ...preferences, [key]: value };
    setPreferences(newPreferences);
    localStorage.setItem("preferences", JSON.stringify(newPreferences));
  };

  const navigate = useNavigate();

  // Handlers for each form component to update Home state
  const handleCustomerTypeChange = (type) => {
    const isBusinessCustomer = type === "business";
    setIsBusiness(isBusinessCustomer);
    if (isBusinessCustomer) {
      setBond(0); // Reset bond if customer is a business
    }
  };
  const handleBondChange = (newBond) => {
    if (!isBusiness) {
      setBond(newBond);
    }
  };
  const handleUnderWarrantyChange = (isUnderWarranty) => setUnderWarranty(isUnderWarranty);
  const handleCustomerDetails = (details) => setCustomerDetails(details);
  const handleRepairDetails = (details) => setRepairDetails(details);
  const handleCostChange = (details) => setCostDetails(details);

  // Function to handle form submission
  const onSubmit = async (event) => {
    event.preventDefault();
    navigate("/invoice", {
      state: { customerDetails, repairDetails, bond, underWarranty, courtesyItems, costDetails, isBusiness },
    });
  };

  //Component UI: HTML Rendering
  return (
    <form className="row" style={{ minHeight: "60vh" }} onSubmit={onSubmit}>
      <div className="col-12 col-lg-4 p-4 m-0" style={{ minHeight: "30vh", backgroundColor: "#FCF3CF" }}>
        <FormCustomerDetail onCustomerTypeChange={handleCustomerTypeChange} setCustomerDetails={handleCustomerDetails} />
      </div>
      <div className="col-12 col-lg-4 p-4 m-0" style={{ minHeight: "30vh", backgroundColor: "#D5F5E3" }}>
        <FormRepairDetail onWarrantyChange={handleUnderWarrantyChange} setRepairDetails={handleRepairDetails} />
      </div>
      <div className="col-12 col-lg-4 p-0 m-0">
        <div className="p-4" style={{ minHeight: "30vh", backgroundColor: "#2874A6" }}>
          <FormCourtesyPhone onBondChange={handleBondChange} isBusiness={isBusiness} setCourtesyItems={setCourtesyItems} />
        </div>
        <div className="p-4" style={{ minHeight: "20vh", backgroundColor: "#EDBB99" }}>
          <FormCost bond={bond} isUnderWarranty={underWarranty} onCostChange={handleCostChange}/>
        </div>
      </div>
      <div className="p-4 text-center" style={{ minHeight: "10vh", backgroundColor: "#EDBB99" }}>
        <FormButtons />
      </div>
      <div className="p-4 text-center">
        <h4>Set Invoice Preferences</h4>
        <button
          type="button"
          onClick={() => handlePreferenceChange("fontSize", "14px")}
          style={{
            border: preferences.fontSize === "14px" ? "2px solid red" : "none",
          }}
        >
          Small Font
        </button>
        <button
          type="button"
          onClick={() => handlePreferenceChange("fontSize", "16px")}
          style={{
            border: preferences.fontSize === "16px" ? "2px solid red" : "none",
          }}
        >
          Medium Font
        </button>
        <button
          type="button"
          onClick={() => handlePreferenceChange("fontSize", "18px")}
          style={{
            border: preferences.fontSize === "18px" ? "2px solid red" : "none",
          }}
        >
          Large Font
        </button>
        <button
          type="button"
          onClick={() => handlePreferenceChange("textColor", "#000000")}
          style={{
            border: preferences.textColor === "#000000" ? "2px solid red" : "none",
          }}
        >
          Black Text
        </button>
        <button
          type="button"
          onClick={() => handlePreferenceChange("textColor", "#ff0000")}
          style={{
            border: preferences.textColor === "#ff0000" ? "2px solid red" : "none",
          }}
        >
          Red Text
        </button>
        <button
          type="button"
          onClick={() => handlePreferenceChange("backgroundColor", "#ffffff")}
          style={{
            border: preferences.backgroundColor === "#ffffff" ? "2px solid red" : "none",
          }}
        >
          White Background
        </button>
        <button
          type="button"
          onClick={() => handlePreferenceChange("backgroundColor", "#f0f0f0")}
          style={{
            border: preferences.backgroundColor === "#f0f0f0" ? "2px solid red" : "none",
          }}
        >
          Gray Background
        </button>
      </div>
    </form>
  );
}

export default Home;